.paying-attention-edit-block {
    padding: 20px;
    border-radius: 2px;
    border: 1px solid #b6a0bd;
    background-color: #f4e9ff;
  }

  .mark-as-correct{
    
    color: #ffd710;
    position: relative;
    top: -3px;
    transition: transform 0.3s ease-out;
    
    
    &:hover{
      transform: scale(1.25) rotate(12deg);
    }

  }


  .attention-delete{
    color:#ff0000!important;
    position: relative;
    top: -5px;

    &:hover{
      color: #c20000!important;;
    }
  }